/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

 @import "https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Newsreader:ital,opsz,wght@0,6..72,200..800;1,6..72,200..800&display=swap";


img {
	max-width: 100%;
	object-fit: cover;
}

.footer-contact {
	.editor-post-featured-image__preview,
	.editor-post-featured-image__toggle {
		height: 100%;
		max-height: none;
		position: relative;
	}
}

.editor-post-featured-image__preview .media-wrap { width: 100%; height: 100%; position: relative; }
.editor-post-featured-image__preview .select-image-overlay { position: absolute; width: 100%; background: rgb(0 0 0 / 20%); left: 0; top: 0; color: #fff; display: flex; justify-content: center; align-items: center; opacity: 0; transition: opacity 0.3s; height: 100%; }
.editor-post-featured-image__preview .pseudo-btn { border: 1px solid #fff; padding: 0.75rem 1rem; border-radius: 4px; }
.editor-post-featured-image__preview:hover .select-image-overlay { opacity: 1; }